import React from "react"
import propTypes from "prop-types"
import DeleteSavedBook from "./DeleteSavedBook"
import styled from "styled-components"

export default function BookDetailed({ book, drawDeleteButton }) {
  const fake_book = {
    id: "ovo4tAEACAAJ",
    volumeInfo: {
      title: "Harry potter e la pietra filosofale",
      authors: ["J.K Rowling"],
      categories: ["Science", "Fiction"],
      publishDate: "2021-09-10T18:00:00.000",
      pageCount: 491,
      description:
        "Lorem ipsum dolor sit amet consectetur adipisicing elit. Quasi iusto eum modi delectus eos dolorum minima, aliquid fugiat molestiae praesentium ad explicabo ipsam possimus, optio error, aspernatur corrupti vel! Facere? Autem, laborum asperiores blanditiis atque modi repellat fugiat aperiam eligendi error rerum quisquam, sint aut odit at repellendus recusandae alias nemo dolor necessitatibus veritatis quam voluptatem magni eos aliquid. Amet.",
    },
  }

  const { title, imageLinks, description } = fake_book.volumeInfo
  const authors = fake_book.volumeInfo.authors?.join(", ")
  const categories = fake_book.volumeInfo.categories?.join(" ")
  let year = ""
  if (fake_book.volumeInfo.publishedDate?.length > 0) {
    year = new Date(fake_book.volumeInfo.publishedDate).getFullYear()
  }
  const numPages = fake_book.volumeInfo?.pageCount
  const image = imageLinks?.thumbnail

  return (
    <Wrapper>
      <Header>
        <Title>{title}</Title>
        <Subtitle>
          <b>{title}</b> è un libro {authors ? `scritto da ${authors}.` : ""}{" "}
          {authors && year ? " e " : ""} {year ? `pubblicato nel ${year}.` : ""}
          Appartiene al genere <b>{categories}</b> e conta {numPages} pagine
        </Subtitle>
        {drawDeleteButton && <DeleteSavedBook bookId={fake_book.id} />}
      </Header>
      <Body>
        {description && (
          <DescriptionWrapper>
            <DescriptionTitle>Trama</DescriptionTitle>
            <DescriptionText>{description}</DescriptionText>
          </DescriptionWrapper>
        )}
      </Body>
    </Wrapper>
  )
}

const Body = styled.div``

const DescriptionWrapper = styled.div``

const DescriptionTitle = styled.h2``

const DescriptionText = styled.p`
  text-align: justify;
  font-size: 20px;
  line-height: 1.5;
`

const Header = styled.header``

const Wrapper = styled.div`
  width: 100%;
  display: grid;
  gap: 30px;
`

const Title = styled.h1`
  font-size: 48px;
  text-align: center;
  line-height: 1.1;
`
const Subtitle = styled.h3`
  font-weight: 300;
  padding: 20px;
  font-size: 22px;
  line-height: 1.3;
  text-align: justify;
`
BookDetailed.propTypes = {
  book: propTypes.shape({
    id: propTypes.string.isRequired,
    volumeInfo: propTypes.shape({
      title: propTypes.string.isRequired,
      imageLinks: propTypes.string.isRequired,
      description: propTypes.string.isRequired,
      authors: propTypes.instanceOf(Array) || propTypes.instanceOf(String),
      categories: propTypes.instanceOf(Array),
      publishedDate: propTypes.instanceOf(Date),
      pageCount: propTypes.number.isRequired,
    }),
  }).isRequired,
  drawDeleteButton: propTypes.bool.isRequired,
}
