import styled from "styled-components"

export default class SavedBooks extends Component {
  constructor(props) {
    super(props)
    this.state = {
      books: [],
      isLoading: false,
    }
  }

  render() {
    if (this.state.isLoading) {
      return <Loader />
    }

    return (
      <Wrapper>
        {/* <GoBackButton destination={"/"} /> */}
        <Title>Libri salvati</Title>
        <BooksWrapper>
          <SavedBooksProvider>
            <SavedBooksContext.Consumer>
              {/* {({ books }) => {
                if (books.length === 0) {
                  return <h2>Nessun libro salvato</h2>
                }
                return books.map(book => (
                  <Book
                    key={book.id}
                    id={book.id}
                    drawGoBack={false}
                    drawDeleteButton={true}
                  />
                ))
              }} */}

              {({ books }) => {
                return (
                  <Book
                    id="ovo4tAEACAAJ"
                    drawGoBack="false"
                    drawDeleteButton={true}
                  />
                )
              }}
            </SavedBooksContext.Consumer>
          </SavedBooksProvider>
        </BooksWrapper>
      </Wrapper>
    )
  }
}

